.spaceOverlay {
  height: 100%;
  width: 100%;
  padding: 50px 0;
  background: url('/ugh.jpg');
  background-size: cover;
  -webkit-box-shadow: 0px -1px 28px 10px rgba(0,0,0,0.75);
          box-shadow: 0px -1px 28px 10px rgba(0,0,0,0.75);
}

.bubble {
  width: 70%;
  margin: 0 auto;
  display: block;
}

.spaceShape {
  -webkit-box-shadow: inset 0px 0px 27px -1px rgba(0,0,0,0.75);
          box-shadow: inset 0px 0px 27px -1px rgba(0,0,0,0.75);
}

.breathe {
  -webkit-animation: breathe 3s infinite ease-in-out;
          animation: breathe 3s infinite ease-in-out;
}

.titleText {
  font-size: 150px;
  letter-spacing: 10px;
}

.secondText {
  font-size: 127px;
  letter-spacing: 5px;
}

.thirdText {
  font-size: 127px;
  letter-spacing: 5px;
}

.countdown {
  font-size: 114px;
  letter-spacing: 15px;
}

@-webkit-keyframes breathe{
  50%{
    -webkit-transform: scaleY(1.03);
            transform: scaleY(1.03);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}

@keyframes breathe{
  50%{
    -webkit-transform: scaleY(1.03);
            transform: scaleY(1.03);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}




